.floating-menu {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 100%;
  height: 2.1rem !important;
  width: 2.1rem !important;
  pointer-events: auto !important;
  display: flex;
  position: fixed;
  background-color: white !important;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.floating-menu-item {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 100%;
  height: 2.1rem;
  width: 2.1rem;
  pointer-events: auto !important;
  display: flex;
  opacity: 1;
  position: static !important;
  position: initial !important;
  background-color: white !important;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease-in;
}

.floating-menu > svg {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 0;
  height: 32px;
  margin: auto;
  width: 32px;
  transition: all 0.2s ease-in;
}

.float-select {
  width: 100%;
  margin: 0;
  padding: 0;
  height: 100%;
  position: relative;
  cursor: pointer;
  z-index: 2;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: none;
  opacity: 0;
}

.float-select:checked + svg {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.float-select:checked ~ .float-container {
  display: flex;
  left: 40px;
  opacity: 1;
}

.float-container {
  display: none;
  opacity: 0;
  flex-direction: row;
