@media (max-width: 480px) {
  .tray {
    width: calc(100vw - 2rem - 10px);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    margin-left: 1rem;
    margin-right: 1rem;
  }

  .note-bg {
  	background-size: auto 120%;
  }

  .note-card {
    width: calc(100vw * 0.5 - 4rem - 5px);
    margin-right: 0;
  }

  .choice {
