dialog {
  z-index: 16;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0;
  pointer-events: none;
  opacity: 0;
  border: none;
  background-color: rgba(0, 0, 0, 0.473);
  transition: opacity 0.5s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
dialog[open] {
  opacity: 1;
  pointer-events: inherit;
}

