.fragment {
  height: 500px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  scroll-behavior: smooth;
  flex-direction: column;
  flex-wrap: wrap;
}

.super {
  height: 500px;
  position: relative;
}

.imageBack {
  position: relative;
  width: 100vw;
  height: 500px;
}

.forward {
  position: absolute;
  top: 200px;
  right: 10px;
}

.backward {
  position: absolute;
  top: 200px;
  left: 10px;
}
